*{
    font-family: sans-serif;
    font-size: 17px;
    font-weight: 400;
    font: bolder;
    
    
}

.col{
    display: flex;
}

body{
    background-color: rgb(77, 113, 230);
}



.col-lg-4{
    line-height: 30px;
    width: 250px;
    border-radius: 10px;
    margin-top: 40px;
}

.card{
    border-radius: 20px;
    margin-right: 20px;
    transition: 0.2s;
}

.card:hover{
    transform: scale(1.1);
}
